  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Quicksand, sans-serif;
}

a {
  display: inline-block;
  min-width: 44px;
  min-height: 44px;
}

.skip {
  position: absolute;
  top: -40px;
  left: 0;
  background-color: #111;
  color: white;
  padding: 10px;
  z-index: 100;
}

.skip:focus {
  top: 0;
}

/*
  AppBar
*/
#header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #333;
  padding: 10px;
}

#hamburgerButton {
  display: none;
}

#header .logo img {
  width: 50px;
  min-width: 44px;
  min-height: 44px;
}

#header ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
}

#header ul li {
  display: inline;
}

#header ul li a {
  display: inline-block;
  padding: 0.5rem 1.5rem;
  color: white;
  text-decoration: none;
  min-height: 44px;
  min-width: 44px;
}

#header ul li a:hover {
  background-color: #111;
  border-radius: 999px;
  transition: 0.1s ease-in-out all;
  font-weight: bold;
}

/* appbar tutup */

.hero_image img {
  max-width: 100%;
  max-height: 500px;
  margin: auto;
  display: block;
}

.tentang {
  padding: 150px 120px;
}

.tentang h1 {
  text-align: center;
  margin-bottom: 20px;
}

.tentang p {
  text-align: center;
  padding: 0 10%;
}

