}

.cari button:hover {
  background-color: #111;
} */

.daftar-resto {
  padding: 20px;
}

h2 {
  text-align: center;
  margin-bottom: 20px;
  margin: auto;
  margin-top: 10px;
}

a {
  text-decoration: none;
}

#restoran {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
}

.restoran {
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 300px;
  padding: 20px;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto auto auto;
  row-gap: 10px;
  align-items: center;
  transition: transform 0.3s ease;
  color: #111;
}

.restoran:hover {
  transform: scale(0.95);
}

.restoran img {
  width: 100%;
  border-radius: 5px;
}

.restoran-info h2 {
  margin-top: 0;
  margin-bottom: 5px;
}

.restoran-info p {
  margin: 10px;
}

.btn-detail {
  background-color: #402b3a;
  color: white;
  padding: 10px 20px;
  margin: 10px 0;
  border: none;
  cursor: pointer;
  border-radius: 5px;
  text-align: center;
  display: inline-block;
  text-decoration: none;
  font-size: 16px;
}

.btn-detail:hover {
  background-color: #d63484;
}

/* bagian detail */

.container {
  margin: 0 auto;
  background-color: #f5f5f5;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.restaurant-details {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.restaurant-details img {
  max-width: 300px;
  height: auto;
  margin-right: 20px;
  border-radius: 18px;
}

.info h1 {
  margin-top: 0;
}

.menu table {
  width: 100%;
  border-collapse: collapse;
}

.menu th,
.menu td {
  padding: 8px;
  text-align: left;
  border-bottom: 1px solid #ddd;
}

.menu th {
  background-color: #f2f2f2;
}

.reviews {
  margin-top: 20px;
}

.review {
  border: 1px solid #ddd;
  padding: 10px;
  margin-bottom: 10px;
}

.review-date {
  font-size: 0.8em;
  color: #888;
  margin-left: 10px;
}

/*
  Like
*/
.like {
  font-size: 18px;
  position: fixed;
  bottom: 16px;
  right: 16px;
  background-color: #402b3a;
  color: rgb(255, 255, 255);
  border: 0;
  border-radius: 50%;
  width: 55px;
  height: 55px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

.liked {
  font-size: 18px;
  position: fixed;
  bottom: 16px;
  right: 16px;
  background-color: #d63484;
  color: #402b3a;
  border: 0;
  border-radius: 50%;
  width: 55px;
  height: 55px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

aside {
  width: 100%;
}

.sosial {
  background-color: #f2f2f2;
  padding: 20px;
  border-radius: 5px;
}

.sosial h3 {
  margin-bottom: 20px;
  text-align: center;
}

.akun {
  display: grid;
  margin: auto;
  grid-template-columns: 1fr 1fr 1fr;
  text-align: center;
}

.akun a {
  font-weight: bold;
  min-height: 44px;
  min-width: 44px;
}

.instagram {
  color: #833ab4;
}

.linkedin {
  color: #0077b5;
}

.whatsapp {
  color: #25d366;
}

footer {
  background-color: #333;
  color: white;
  padding: 20px;
  text-align: center;
}

@media only screen and (max-width: 1200px) {
  #header {
    padding: 5px;
  }

  #header ul li a {
    padding: 8px;
  }
  .hero_image img {
    width: 100%;
  }

  .tentang {
    padding: 40px;
  }

  .cari input {
    width: 60%;
  }

  .restoran {
    width: 250px;
  }
}

@media only screen and (max-width: 768px) {
  #header {
    flex-direction: column;
    align-items: center;
    padding: 10px;
  }

  #header ul {
    margin-top: 10px;
    display: none;
  }

  #header img {
    max-width: 44px;
    max-height: 44px;
  }

  #hamburgerButton {
    display: block;
    background: transparent;
    border: none;
    color: #fff;
    font-size: 24px;
    cursor: pointer;
    position: absolute;
    top: 15px;
    right: 15px;
    min-height: 44px;
    min-width: 44px;
  }

  #hamburgerButton:hover {
    cursor: pointer;
  }

  #header ul.open {
    display: block;
  }

  #header ul li {
    margin-bottom: 5px;
  }

  #header ul li a {
    padding: 8px 12px;
  }
  .hero_image img {
    max-width: 768px;
  }

  .tentang {
    padding: 30px;
  }

  .cari {
    flex-direction: column;
    align-items: center;
  }

  .cari input {
    width: 80%;
    margin-right: 0;
    margin-bottom: 10px;
  }

  .cari button {
    margin-right: 0;
  }

  .daftar-resto {
    padding: 10px;
  }

  .restoran {
    width: 90%;
  }

  .container {
    padding: 10px;
  }

  .restaurant-details {
    display: block;
    margin-bottom: 10px;
  }

  .restaurant-details img {
    max-width: 100%;
    height: auto;
    display: block; /* Memastikan gambar ditampilkan sebagai blok */
  }
}
